import React, { useState } from 'react';
import '../style/ParkPage/ParkingDetail.css';

function ParkingDetail() {
  const [activeIndex, setActiveIndex] = useState(null);

  const toggleAccordion = (index) => {
    if (activeIndex === index) {
      setActiveIndex(null); //     
    } else {
      setActiveIndex(index); //   
    }
  };

  return (
    <div className="accordion-container">
      <div className="accordion-item">
        <div className="accordion-title" onClick={() => toggleAccordion(0)}>
          <h2> </h2>
          <span>{activeIndex === 0 ? '' : ''}</span>
        </div>
        {activeIndex === 0 && (
          <div className="accordion-content">
            <h3></h3>
            <p className="custom-p">:</p>
            <ul>
              <li> 1  ( 10  1,000)</li>
            </ul>
            <p className="custom-p">:</p>
            <ul>
              <li> 1  ( 10 300)</li>
            </ul>
            <h3>   </h3>
            <ul>
              <li> 1 </li>
              <li>  3</li>
              <li> : 3( 10 1,000)</li>
            </ul>
            <h3></h3>
            <p className="custom-p"> 02-xxxx-xxxx</p>
            <p className="custom-p">
              /        ( 1F
               )
            </p>
          </div>
        )}
      </div>
      <div className="accordion-item">
        <div className="accordion-title" onClick={() => toggleAccordion(1)}>
          <h2>  </h2>
          <span>{activeIndex === 1 ? '' : ''}</span>
        </div>
        {activeIndex === 1 && (
          <div className="accordion-content">
            <p className="custom-p">2  </p>
          </div>
        )}
      </div>
      <div className="accordion-item">
        <div className="accordion-title" onClick={() => toggleAccordion(2)}>
          <h2>  </h2>
          <span>{activeIndex === 2 ? '' : ''}</span>
        </div>
        {activeIndex === 2 && (
          <div className="accordion-content">
            <p className="custom-p"> (xxx)</p>
            <p className="custom-p"> (1x-1xx)</p>
            <p className="custom-p">custom</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default ParkingDetail;
